body {
  width: 1200px;
  margin: 0 auto;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

img {
  border-radius: 4px;
  display: block;
}

.loader.is-hidden, .error.is-hidden {
  display: none;
}

.breed-select {
  margin-top: 20px;
}

.loader {
  margin-top: 36px;
  display: inline-block;
  position: relative;
}

.loader:before {
  content: "Loading";
  color: #5897fb;
  letter-spacing: 2px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  animation: floating 1s ease-out infinite alternate;
  display: inline-block;
}

.loader:after {
  content: "";
  width: 100%;
  height: 10px;
  filter: blur(4px);
  box-sizing: border-box;
  background: rgba(0, 0, 0, .15);
  border-radius: 50%;
  animation: animloader 1s ease-out infinite alternate;
  position: absolute;
  top: 100%;
  left: 0;
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-25px);
  }
}

@keyframes animloader {
  0% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1.2);
  }
}

.cat-info {
  margin-top: 20px;
  display: flex;
}

.container {
  background-color: rgba(88, 151, 251, .04);
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .2);
}

